<template>
  <div id="app">
    <el-container>
      <el-header class="header-container">
        <div class="header-buttons">
          <el-button type="primary" @click="updateChartData">Update Chart Data</el-button>
        </div>
        <h1 class="header-title">Vue with Element-plus and ECharts (Pie Chart)</h1>
      </el-header>
      <el-main>
        <div ref="chartRef" class="chart-container"></div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import { defineComponent, onMounted, ref } from 'vue';
import * as echarts from 'echarts';

export default defineComponent({
  name: 'App',
  setup() {
    const chartRef = ref(null);
    let chartInstance = null;

    const initialData = [
      { value: 1048, name: 'Search Engine' },
      { value: 735, name: 'Direct' },
      { value: 580, name: 'Email' },
      { value: 484, name: 'Union Ads' },
      { value: 300, name: 'Video Ads' }
    ];

    const updateChartData = () => {
      // 这里可以放置更新图表数据的逻辑
      // 例如，从服务器获取新数据，或者简单地使用预设的更新数据
      const updatedData = [
        { value: 1200, name: 'Search Engine' },
        { value: 800, name: 'Direct' },
        { value: 600, name: 'Email' },
        { value: 550, name: 'Union Ads' },
        { value: 350, name: 'Video Ads' }
      ];
      if (chartInstance) {
        chartInstance.setOption({
          series: [{
            // 假设图表类型为饼图，并且数据在第一个系列中
            data: updatedData
          }]
        });
      }
    };

    onMounted(() => {
      if (chartRef.value) {
        chartInstance = initChart(chartRef.value, initialData);
      }
    });

    function initChart(dom, data) {
      const chart = echarts.init(dom);
      const option = {
        title: {
          text: 'Pie Chart Example',
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          left: 'left'
        },
        series: [
          {
            name: 'Access From',
            type: 'pie',
            radius: '50%',
            data: data,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      };
      chart.setOption(option);
      return chart;
    }

    return {
      updateChartData,
      chartRef
    };
  }
});
</script>

<style scoped>
.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.header-buttons {
  margin-right: -20px; /* Adjust based on your layout needs */
}

.header-title {
  margin: 0;
}

.chart-container {
  width: 100%;
  height: 400px;
}
</style>